import { Button, Modal, Box, Typography } from "@material-ui/core";
import React, { useState } from "react";
import Summary from "./Summary";

const CheckOut = (props) => {
  const [open, setOpen] = useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);
  const style = {
    textAlign: "center",
    width: "40%",
    backgroundColor: "pink",
    opacity: "0.9",
    // height: "50%",
    border: "1px solid pink",
    borderRadius: "10px",
    overflowY: "auto",
  };
  return (
    <div style={{ display: "flex", justifyContent: "center" }}>
      <Button onClick={handleOpen} sx={{ color: "pink" }} variant="contained">
        CheckOut
      </Button>
      <Modal
        open={open}
        onClose={handleClose}
        style={{
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
          margin: "10px 0",
        }}
      >
        <Box sx={style}>
          <Typography variant="h6" component="h2">
            <span>Summary</span>
          </Typography>
          <div
            style={{
              display: "flex",
              flexDirection: "row",
              flexWrap: "wrap",
              justifyContent: "space-between",
              padding: "15px 34px",
              alignItems: "center",
            }}
          >
            <h4 style={{ width: "50px" }}>Name</h4>
            <h4>Amount</h4>
            <h3>Price</h3>
          </div>
          <Typography sx={{ mt: 2 }}>
            <Summary items={props?.items} />
          </Typography>
          <div
            style={{
              display: "flex",
              flexDirection: "row",
              flexWrap: "wrap",
              justifyContent: "space-between",
              padding: "15px 34px",
              alignItems: "center",
            }}
          >
            <h4>Total Price : </h4>
            <h2>{props.total}</h2>
          </div>

          <Button>Place Order</Button>
        </Box>
      </Modal>
    </div>
  );
};

export default CheckOut;
