import React from "react";

const Summary = (props) => {
  const contentSummary = props?.items.map((item) => {
    const textStyle = { width: "50px", fontWeight: "500" };

    if (item.amount !== 0) {
      return (
        <div
          style={{
            display: "flex",
            flexDirection: "row",
            flexWrap: "wrap",
            justifyContent: "space-between",
            padding: "15px 34px",
            alignItems: "center",
          }}
        >
          <h4 style={textStyle}>{item.name}</h4>
          <strong style={textStyle}>{item.amount}</strong>
          <strong style={textStyle}>{item.price * item.amount}</strong>
        </div>
      );
    }
  });
  return <div>{contentSummary}</div>;
};

export default Summary;
