import { React, useState } from "react";
import CheckOut from "./CheckOut";
import Ingredient from "./Ingredient";
import PriceSection from "./PriceSection";
import Selector from "./Selector";

const Body = () => {
  const [items, setItems] = useState([
    { name: "breadTop", amount: 1, price: 40 },
    { name: "bacon", amount: 0, price: 20 },
    { name: "cheese", amount: 0, price: 25 },
    { name: "humburger", amount: 0, price: 40 },
    { name: "ketchup", amount: 0, price: 20 },
    { name: "mayonnaise", amount: 0, price: 20 },
    { name: "mustard", amount: 0, price: 25 },
    { name: "pickles", amount: 0, price: 10 },
    { name: "salad", amount: 0, price: 20 },
    { name: "spycySauce", amount: 0, price: 10 },
    { name: "tomatoSlices", amount: 0, price: 30 },
    { name: "breadBottom", amount: 1, price: 40 },
  ]);
  const [total, setTotal] = useState(0);
  const getName = (item, operation) => {
    const list = [...items];
    list.forEach((ele) => {
      switch (operation) {
        case 0:
          ele.name === item.name && ele.amount++;
          break;
        case 1:
          ele.name === item.name && ele.amount--;
          break;
        default:
          console.log("Sorry");
      }
    });
    setItems(list);
    console.log(items);
  };
  return (
    <div>
      <Ingredient items={items} />
      <PriceSection items={items} setTotal={setTotal} />
      <CheckOut items={items} total={total} />
      <Selector items={items} getName={getName} />
    </div>
  );
};

export default Body;
